
.top{
    &.fix{
        position: fixed;
        top:0;
        left:0;
        z-index:99;
        background-color: white;
        width: 100%;
        .top__nav{
            ul{
                padding-top: 10px;
            }
            .icon-menu{
                padding-top: 10px;
            }
        }
    }
    &__nav{
        
        width: 100%;
        box-shadow: 0 10px 10px -2px rgba(0, 0, 0, 0.123);
        min-height: 10px;
        border-bottom:2px solid $light-blue;
        position: relative;
        
        .icon-menu{
            padding-top: 50px;
            transition: .4s;
            display: none;
            color: $light-blue;
            position: relative;
            z-index: 100;
            background-color: white;
            font-size:2.4rem;
            font-weight: 800;
        }

        text-align: center;

        ul{
        display: flex;
        list-style-type: none;
        margin: 0 auto;
        max-width: 1200px;
        justify-content: flex-end;
        transition:.4s;
       padding-top: 40px;
        
            li{               
                transition: .4s;
                
                a{
                    
                    position: relative;
                    display: block;
                    padding: 10px 30px;
                    overflow: hidden;
                    color:$light-blue;
                    letter-spacing: 1px;
                    text-transform: uppercase;
                    cursor: pointer;
                    font-size: 18px;
                    line-height: 42px;
                    transition: .5s;
                    border-top-left-radius: 8px;
                    border-top-right-radius: 8px;

                    &:after{
                        content: '';
                        width: 100%;
                        display: block;
                        height: 100%;
                        background-color: $light-blue;
                        position: absolute;
                        transition:0.5s;
                        left: 0;
                        top:0;
                        transform: translateY(100%);
                        z-index: -1;
                        will-change: transform;
                    }
                   

                    &:hover{
                        color: white;
                        text-decoration: none;

                        &::after{
                            transform: translateY(0);
                        }
                        
    
                    }
                
                  
                   
                   
                }
            }

           
        }
        @media (max-width: 767px){
            .icon-menu{
                display: block;
            }
            ul{
                padding-top: 0px;
                position: absolute;
                width: 100%;
                top:100%;
                transform: translateY(-100%);
                left: 0;
                flex-direction: column;
                z-index: 0;
                transition: .5s;
                background-color: $light-blue;
                border-bottom: 1px solid black;
                opacity:0.5;
                box-shadow: 0 10px 10px -2px rgba(0, 0, 0, 0.123);

                &.active{
                    transform: translateY(0);
                    opacity:1;
    
                }
                li{
                    a{
                        color: white;
                    }
                }
            }

        }

    }
}
